<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        class="mr-3"
        color="primary">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah data
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Konsultan
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="dataKonsultan.nama"
            label="Nama Konsultan"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="dataKonsultan.email"
            label="E-mail"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="dataKonsultan.nohp"
            label="No Hp/WA"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <div class="subtitle-1">Status</div>
          <v-radio-group 
            row 
            v-model="statusKonsultan" 
            class="mt-0 d-flex"
            validate-on-blur>
            <v-radio
              label="Aktif"
              :value="'true'"
            ></v-radio>
            <v-radio
              label="Tidak"
              :value="'false'"
            ></v-radio>
          </v-radio-group>

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataKonsultan: {
          nama: '',
          email: '',
          nohp: '',
          status: ''
        },
        statusKonsultan: '',
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil ditambahkan',
          error: 'Data gagal ditambahkan',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: {
      ...mapState('dataKonsultan', ['isLoading', 'status'])
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          if (this.statusKonsultan === 'true') {
            this.dataKonsultan.status = true
          } else {
            this.dataKonsultan.status = false
          }
          this.$store.dispatch('dataKonsultan/postDataKonsultan', this.dataKonsultan)
          this.$store.dispatch('dataKonsultan/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataKonsultan/updateStatus', null)
      }
    }
  }
</script>
