<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-0"
			:headers="header"
			:items="dataKonsultan"
			:items-per-page="10">		
	    <template v-slot:item.no="{item}">
	    	{{dataKonsultan.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>	
			<template v-slot:item.status="{ item }">
	      <v-btn 
	      	style="border-radius: 2px 0 0 2px"
	      	x-small
	      	@click="item.status = true"
	      	:color="item.status === true?'success':'default'"
	      	depressed>
	      	on
	      </v-btn>
	      <v-btn 
	      	style="border-radius: 0 2px 2px 0"
	      	x-small
	      	@click="item.status = false"
	      	:color="item.status === false?'red':'default'"      	
	      	:dark="item.status === false?true:false" 
	      	depressed>
	      	off
	      </v-btn>
	    </template>
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<EditKonsultan :data="item" />
				<HapusKonsultan :id="item.id" />				
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'
	import EditKonsultan from './EditKonsultan'
	import HapusKonsultan from './HapusKonsultan'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'nama', sortable: false },
				// { text: 'Username', value: 'user', sortable: false },
				{ text: 'Password', value: 'password', sortable: false },
				{ text: 'Kantor Unit', value: 'unit.nama_kepala', sortable: false },
				{ text: 'No HP/WA', value: 'nohp', sortable: false },
				{ text: 'Aktifasi', value: 'status', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],
		}),

		components: {
			HapusKonsultan,
			EditKonsultan,
			Loader
		},

		created() {
			this.$store.dispatch('dataKonsultan/getDataKonsultan')
			this.$store.dispatch('dataKonsultan/updateIsLoading', true)
		},

		computed: {
			...mapState('dataKonsultan', ['dataKonsultan', 'isLoading'])
		}
	}
</script>